import React, { useEffect, useState } from 'react';
import axios from "axios";
import Form from 'react-bootstrap/Form';
import { Button } from '@mui/material';
import { useGetUserID } from "../hooks/useGetUserID";
import { useParams, useNavigate } from "react-router-dom";
import { NavBar } from '../components/navbar';

export const Updateexpense = () => {
  const { id } = useParams();
  const userID = useGetUserID();
  const navigate = useNavigate();

  const [eamount, setEamount] = useState();
  const [edescription, setEdescription] = useState();

  useEffect(() => {
    // axios.get(`http://localhost:3001/add/expense/${id}`)
    axios.get(`/api/add/expense/${id}`)
      .then(result => {
        console.log(result);
        setEamount(result.data.eamount);
        setEdescription(result.data.edescription);
      })
      .catch(error => console.log(error));
  }, [id]);

  const Update = (e) => {
    e.preventDefault();
    // axios.put(`http://localhost:3001/add/expenseupdate/${id}`, { eamount, edescription })
    axios.put(`/api/add/expenseupdate/${id}`, { eamount, edescription })
      .then(result => {
        console.log(result);
        navigate('/home');
      })
      .catch(err => console.log(err));
  };

  return (
    <div>
      <style>
      {`
        body {
          background-image: url("https://media.istockphoto.com/id/1326516070/vector/abstract-modern-background-with-hexagon-element-and-silver-white-gradient-color.jpg?s=612x612&w=0&k=20&c=pQuqvEqotCNng1vqOnTM9J02HdLLQfWzaqilC5cgR_c=");
          margin: 0;
          padding: 0;
          background-repeat: no-repeat;
          background-size: cover;
        }
      `}
    </style>
      <NavBar/>
      <p style={{marginLeft:"5px",marginTop:"15px",fontSize:"40px", color:"#5A96E3", fontFamily:"sans-serif", fontWeight: "bold" }}>Update Your Expense </p>
      <div className="add" style={{paddingRight:"4px",paddingLeft:"4px",paddingTop:"20px",paddingBottom:"20px"}}>
      <Form  onSubmit={Update}>
        <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
          <Form.Label>Your Expense</Form.Label>
          <Form.Control type="number" value={eamount} onChange={(e) => setEamount(e.target.value)} />
        </Form.Group>
        <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
          <Form.Label>Description</Form.Label>
          <Form.Control as="textarea" value={edescription} onChange={(e) => setEdescription(e.target.value)} rows={3} />
        </Form.Group>
        <Button type='submit' variant="outlined" >
          Update Expense
        </Button>
      </Form>
      </div>
    </div>
  );
};


